import React from "react";
import DTimage from "../../img/DT.png";
import Media from "react-media";
import "../../style/css/dt.css";

const Dt = () => {
	return (
		<>
			<Media query="(min-width: 992px)">
				{(matches) => {
					return matches ? (
						<section className="mgr-Dagmara-Toporowska-container" id="section3">
							<div className="DT-container">
								<div className="DT-img">
									<img src={DTimage} className="DTimage" />
								</div>
								<div className="DT-description">
									<h2>O NAS</h2>
									<p>
										Dziaalnoci zwizan z tumaczeniami zajmujemy si od roku
										2001. Pocztkowo swoj ofert kierowalimy do osb, ktre
										zainteresowane byy wykonaniem tumacze z jzyka
										angielskiego. Dynamiczny rozwj naszych usug oraz ch
										wyjcia naprzeciw oczekiwaniom kontrahentw sprawiy, e w
										roku 2005 zaoylimy Biuro Tumacze. Doskonale wiemy jak
										pomc naszym Klientom, poniewa sami rwnie dokonujemy
										przekadu. Wacicielk Biura jest tumacz przysigy jzyka
										angielskiego mgr Dagmara Toporowska.
									</p>
									<p>
										Zesp Tumaczy skupiony wok naszego biura zosta dobrany
										bardzo starannie, gdy tylko osobyz odpowiednimi
										predyspozycjami, dowiadczeniem, szerok wiedz merytoryczn
										z rnych dziedzini wielkim zaangaowaniem w prac z
										tumaczeniami mog sprosta zleceniom naszych Klientw.
									</p>
									<p>
										Nieustanne denie do perfekcji, dostosowanie kompleksowej
										oferty do potrzeb Klientw indywidualnych i firm, stawia
										przed nami coraz nowsze wyzwania, ktre z dum realizujemy.
										Zadowolenie Klientw korzystajcych z naszych usug jest dla
										nas priorytetem, dlatego te dooymy wszelkich stara by
										Pastwa usatysfakcjonowa.
									</p>
									<button className="see-offer2">Zobacz ofert</button>
								</div>
							</div>
						</section>
					) : (
						<section className="mgr-Dagmara-Toporowska-container" id="section3">
							<div className="DT-container">
								<div className="DT-description">
									<h2>O NAS</h2>
									<p>
										Dziaalnoci zwizan z tumaczeniami zajmujemy si od roku
										2001. Pocztkowo swoj ofert kierowalimy do osb, ktre
										zainteresowane byy wykonaniem tumacze z jzyka
										angielskiego. Dynamiczny rozwj naszych usug oraz ch
										wyjcia naprzeciw oczekiwaniom kontrahentw sprawiy, e w
										roku 2005 zaoylimy Biuro Tumacze. Doskonale wiemy jak
										pomc naszym Klientom, poniewa sami rwnie dokonujemy
										przekadu. Wacicielk Biura jest tumacz przysigy jzyka
										angielskiego mgr Dagmara Toporowska.
									</p>
									<p>
										Zesp Tumaczy skupiony wok naszego biura zosta dobrany
										bardzo starannie, gdy tylko osobyz odpowiednimi
										predyspozycjami, dowiadczeniem, szerok wiedz merytoryczn
										z rnych dziedzini wielkim zaangaowaniem w prac z
										tumaczeniami mog sprosta zleceniom naszych Klientw.
									</p>
									<p>
										Nieustanne denie do perfekcji, dostosowanie kompleksowej
										oferty do potrzeb Klientw indywidualnych i firm, stawia
										przed nami coraz nowsze wyzwania, ktre z dum realizujemy.
										Zadowolenie Klientw korzystajcych z naszych usug jest dla
										nas priorytetem, dlatego te dooymy wszelkich stara by
										Pastwa usatysfakcjonowa.
									</p>
									<div className="button-and-DT-img">
										<button className="see-offer2">Zobacz ofert</button>
										<div className="DT-img">
											<img src={DTimage} className="DTimage" />
										</div>
									</div>
								</div>
							</div>
						</section>
					);
				}}
			</Media>
		</>
	);
};

export default Dt;
